              "T. Daugirdas cared about the history of Kaunas a lot, his articles and manuscripts from the past of Kaunas city are the evidencec of that. He was actively involved in public life and elected as a member of Kaunas Governorate Archival Commission in 1913. During World War I he remained in Kaunas to take care of the museum, prepared exhibits for a Lithuanian exhibition in Kaliningrad, was elected the assistant burgomaster of Kaunas in 1915. T. Daugirdas wrote the diary of memories of the war years in 1915.",
