              "T. Daugirdo dka, Kauno miesto muziejus virsta tikra tokio vardo verta institucija. Jo nuopelnus labai tiksliai apibdino jos amininkas kultros istorikas M. Brenteinas, pabrdamas, kad i ankstesns besistemins daikt kolekcijos, savo rinkinio pagrindu T. Daugirdas sukr tikrai pavyzdin provincijos muziej, kuris apm kelias sales. Pirmojo Pasaulinio karo metais, per vokiei okupacij, jis liko Kaune saugoti io muziejaus, kad is nebt perimtas ir iardytas.",
